body{
  background-color:black;
}

.App {
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;  
  background-color:black;
}

div#console-window{
  position: absolute;
  z-index: 3;
  width:30%;
  border:1px solid rgba(255, 255, 255, 0.42);
  border-radius: 5px;
  background-color: #333333;
}
@-webkit-keyframes fadein {
  0%   {opacity:0;}
  100% {opacity:1;}
}
@keyframes fadein {
  0%   {opacity:0;}
  100% {opacity:1;}
}
@-webkit-keyframes fadeout {
  0%   {opacity:1;}
  100% {opacity:0;}
}
@keyframes fadeout {
  0%   {opacity:1;}
  100% {opacity:0;}
}

div#console-header>*{
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: center;
      align-self: center;
}
div#console-header h3{
  margin:0px;
}
div#console-header{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: white;
  background-color: rgb(38, 38, 38);
  border-bottom: 1px solid rgb(19, 19, 19);
  padding: 5px;
  padding-right: 10px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
div#console-display{
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  padding: 5px;
  min-height: 100px;
  
  border-bottom: 1px solid black;
}
div#console-input{
  height: 32px;
  
}
div.console-typing{
  padding: 5px;
  color:white;
  background-color: #333333;
  text-align: left;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

div#user-Input{
  display:-ms-flexbox;
  display:flex;
  color:white;
}
div#input-Field{
  padding-top: 5px;
  padding-left: 5px;
  
  color:inherit;
}
div#nav-Bar{
  padding-top:20px;
  background:-webkit-gradient(linear, left top, left bottom, from(black),color-stop(black),to(transparent));
  background:-webkit-linear-gradient(top, black,black,transparent);
  background:-o-linear-gradient(top, black,black,transparent);
  background:linear-gradient(to bottom, black,black,transparent);
  color:white;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  position: absolute;
  /* top: -100%; */
  width: 100%;
  /* height: 150%; */
  z-index: 1;
  top:-165px;
  height: 278px;
}
.nav-item{
  padding-right: 37.5px;
  padding-left: 37.5px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content
}
img#bg-gif{
  position: absolute;
  top:-100%;
  width: 100%;
  height: auto;
  opacity: .5;
  z-index: 0;
}
div#nav-Bar div{
  background-color:transparent;
  color:inherit;
}

div#info-Container{
  background-color:blue;
  position: absolute;
  left: -100%;
}

.blinking-cursor {
  /* font-weight: inherit; */
  padding-top:5px;
  font-size: large;
  -webkit-animation: 1s blink step-end infinite;
  animation: 1s blink step-end infinite;
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: inherit;
  }
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: inherit;
  }
}

@-webkit-keyframes "blink" {
  from, to {
    color: transparent;
  }
  50% {
    color: inherit;
  }
}
.slideLeft {
  -webkit-animation: slideLeft 1s forwards;
  /* -webkit-animation-delay: 2s; */
  animation: slideLeft 1s forwards;
  /* animation-delay: 2s; */
}

@-webkit-keyframes slideLeft {
  100% { left: 0; }
}

@keyframes slideLeft {
  100% { left: 0; }
}
.slideTop {
  -webkit-animation: slideTop 1s forwards;
  /* -webkit-animation-delay: 2s; */
  -webkit-animation-name: slideTop;
          animation-name: slideTop;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

@-webkit-keyframes slideTop {
  100% { top: 0; }
}

@keyframes slideTop {
  100% { top: 0; }
}

.nav-item a{
  color:white;
}

@media only screen and (max-width: 600px) {
  div#console-window{
    width:100%;
  }
  img#bg-gif{
    right:-95%;
    width: auto;
    height: 100%;
  }
  div#nav-Bar{
    padding-top:20px;
    background:-webkit-gradient(linear, left top, right top, from(black),color-stop(black),to(transparent));
    background:-webkit-linear-gradient(left, black,black,transparent);
    background:-o-linear-gradient(left, black,black,transparent);
    background:linear-gradient(to right, black,black,transparent);
    color:white;
    top: -70px;
    left: -100%;
    width: auto;
    z-index: 1;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .nav-item.slide0{
    padding-left:25px;
    border-bottom-right-radius: 15px;
    border-right: 1px solid white;
    border-bottom: 1px solid white;
    z-index: 3;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    height: 45px;
    -ms-flex-align: center;
        align-items: center;
    background-color: black;
  }
}


